.restaurant-card:hover {
  transform: translateY(-5px);
}

.restaurant-card.selected {
  border-color: #4285f4;
}

.restaurant-info {
  display: flex;
  flex-direction: column;
}

.restaurant-name {
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
}

.restaurant-category,
.restaurant-address,
.restaurant-price-range,
.restaurant-rating {
  font-size: 0.875rem;
  margin-bottom: 0.25rem;
}

.restaurant-rating {
  font-weight: bold;
  color: #333;
}
